body {
  background-color: #480ada;
}
.App {
  background-color: #f5f5f5;
  min-height: 500px;
  padding-top: 50px;
  padding-bottom: 50px;
  width: 400px;
  margin: 70px auto;
  border-radius: 20px;
}

#to-do-form input {
  background-color: #f5f5f5;
  border: 0;
  color: #480ada;
  width: 200px;
  height: 50px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 5px;
}
#to-do-form input::placeholder {
  color: #4c00ff;
}
#to-do-form input:focus {
  outline: none;
}
#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: #480ada;
  color: #f5f5f5;
  cursor: pointer;
}
#to-do-form button:focus {
  outline: none;
}
